* {
  box-sizing: border-box;
}

body {
  color: #000;
  background-color: #f6f9fa;
  line-height: 1.5;
}

img {
  border: 1px solid #041d2b;
}

.breed-select {
  max-width: 400px;
  margin: auto;
}

.error .is-hidden {
  visibility: hidden;
}

.cat-info {
  justify-content: center;
  display: flex;
}

.cat-information {
  max-width: 1200px;
  background-color: #e2e9f3;
  justify-content: center;
  gap: 20px;
  margin-top: 20px;
  padding: 20px;
  display: flex;
}

.box-img {
  align-items: center;
  display: flex;
}

.loader-container {
  width: 100%;
  height: 100%;
  z-index: 100;
  background-color: rgba(46, 47, 66, .4);
  justify-content: center;
  align-items: center;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.loader-container.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.loader {
  perspective: 1000px;
  width: 100px;
  height: 100px;
  color: #b8d9f1;
  border-radius: 50%;
  margin: auto;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: rotateZ(45deg);
}

.loader:before, .loader:after {
  content: "";
  width: inherit;
  height: inherit;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  transform: rotateX(70deg);
}

.loader:after {
  color: #1c3cdc;
  animation-delay: .4s;
  transform: rotateY(70deg);
}

@keyframes rotate {
  0% {
    transform: translate(-50%, -50%)rotateZ(0deg);
  }

  100% {
    transform: translate(-50%, -50%)rotateZ(360deg);
  }
}

@keyframes rotateccw {
  0% {
    transform: translate(-50%, -50%)rotate(0deg);
  }

  100% {
    transform: translate(-50%, -50%)rotate(-360deg);
  }
}

@keyframes spin {
  0%, 100% {
    box-shadow: .2em 0;
  }

  12% {
    box-shadow: .2em .2em;
  }

  25% {
    box-shadow: 0 .2em;
  }

  37% {
    box-shadow: -.2em .2em;
  }

  50% {
    box-shadow: -.2em 0;
  }

  62% {
    box-shadow: -.2em -.2em;
  }

  75% {
    box-shadow: 0 -.2em;
  }

  87% {
    box-shadow: .2em -.2em;
  }
}

.loader-text {
  color: #f0f8ff;
  text-align: center;
  margin-top: 250px;
  margin-left: 100px;
  font-size: 30px;
}

#cat {
  height: 297px;
  width: 507.5px;
  align-item: center;
  z-index: 10;
  -ms-animation: sprite-animation 1.2s steps(16, end) infinite;
  background: url("https://res.cloudinary.com/pastelitos/image/upload/v1610526571/eva/gatito_pushui.svg");
  margin-top: 2em;
  animation: sprite-animation 1.2s steps(16, end) infinite;
}

@keyframes sprite-animation {
  from {
    background-position: 0 0;
  }

  to {
    background-position: -8120px 0;
  }
}

